import { Component, Input, Output, EventEmitter, ViewChild, forwardRef, Renderer, ElementRef, OnInit } from '@angular/core';
import { ControlValueAccessor, FormControlName, NG_VALUE_ACCESSOR } from '@angular/forms';
//
import { BaseControl } from './base-control';
import { BaseComponent } from '../base-component';
import { Policies } from '../security/policies';

@Component({
    moduleId: module.id,
    selector: 'az-button',
    templateUrl: './az-button.component.html',
    providers: [
        {
            provide: BaseComponent,
            useExisting: forwardRef(() => AzButtonComponent)
        }
    ]
})
export class AzButtonComponent extends BaseControl {    
    @Input() type: string = 'default';
    //
    @Output() click: EventEmitter<void> = new EventEmitter<void>();
    //
    get buttonClass(): string {
        return "btn-" + this.type;
    }
    //
    onClick() {
        this.click.next();
    }


}